import { loginApi,getUserTokenApi, userInfoUpdateApi } from '@/api/user';
import { changeStringToObject } from '@/utils/index';
//用户模块
export default {
  namespaced: true,
  state(){
    return{
      //存储用户信息
      userInfo:{},
      token:'',
      //存储账号密码
      account:{},
      //存储世界发言
      worldChatBox:[],
      //存储门派发言
      menpaiChatBox:[],
      //存储登录过的账号
      accountArr:[
        // {
        //   zhanghao:'tianhuaban1',
        //   mima:112233,
        //   name:'测试1',
        //   active:false
        // },
        // {
        //   zhanghao:'tianhuaban2',
        //   mima:112233,
        //   name:'测试2',
        //   active:true
        // }
        // {
        //   zhanghao:'tianhuaban3',
        //   mima:112233,
        //   name:'测试3',
        //   active:false
        // }
      ]
    }
  },
  mutations:{
    //设置用户信息
    setUserInfo(state,value){
      state.userInfo = value
    },
    //设置token
    setUserToken(state,value){
      state.token = value
    },
    //设置账号密码
    setAccount(state,value){
      state.account = value
    },
    //清空本地存储
    deleteAllInfo(state){
      state.userInfo = {}
      state.token = ''
      state.account = {}
    },
    //放入世界发言
    setWorldChatBox(state,value){
      state.worldChatBox.push(value)
    },
    //放入门派发言
    setMenpaiChatBox(state,value){
      state.menpaiChatBox.push(value)
    },
    //删除世界发言记录
    deleteWorldChatBox(state){
      state.worldChatBox.shift()
    },
    //删除门派发言记录
    deleteMenpaiChatBox(state){
      state.menpaiChatBox.shift()
    },
    //删除所有聊天记录
    deletaAllChat(state){
      state.worldChatBox = []
      state.menpaiChatBox = []
    },
    //修改账号active
    changeActive(state,zhanghao){
      //如果是切换的账号则变为true
      state.accountArr.forEach(item=>{
        if(item.zhanghao === zhanghao){
          item.active = true
        }
        else{
          item.active = false
        }
      })
    },
    //删除登录过的某个账号
    deleteAccountArr(state,zhanghao){
      let index = state.accountArr.findIndex(item=>item.zhanghao === zhanghao)
      if(index !== -1){
        state.accountArr.splice(index,1)
      }
    },
    //登录账号时 将账号存储到已登录的账号列表
    addAccountArr(state,account){
      state.accountArr.push(account)
    }
  },
  actions:{
    //登录
    async login({commit},userForm){
      //发送登录请求
      const result = await loginApi(userForm)
      //截取状态码 用来判断是否成功 字符串1为成功
      const code = result.substr(0,1)
      //如果登录成功
      if(code==='1'){
        //截取用户信息 并转换为json对象
        let obj = changeStringToObject(result.substr(1,result.length))
        //存储到state
        commit('setUserInfo',obj)
      }
      else {
        //截取错误原因
        let errorInfo = result.substr(1,result.length)
        return Promise.reject(new Error(errorInfo))
      }
    },

    //发送请求获取token
    async getUserToken({commit},userForm){
      const result = await getUserTokenApi(userForm)
      //存储token到state
      commit('setUserToken',result.substr(1,result.length))
      //存储账号密码到state
      commit('setAccount',userForm)
    },

    //数据刷新功能
    async userInfoUpdate({commit,state}){
      const result = await userInfoUpdateApi(state.account)
      //截取状态码 用来判断是否成功 字符串1为成功
      const code = result.substr(0,1)
      //如果更新成功
      if(code === '1'){
        //截取用户信息 并转换为json对象
        let obj = changeStringToObject(result.substr(1,result.length))
        //存储到state
        commit('setUserInfo',obj)
      }
      else {
        //截取更新失败原因
        let errorInfo = result.substr(1,result.length)
        return Promise.reject(new Error(errorInfo))
      }
    },

    //处理世界发言功能 聊天记录最多为100条
    handleWorldChat({commit,state},obj){
      //放入世界消息记录盒子
      commit('setWorldChatBox',obj)
      //如果聊天记录超过50条
      if(state.worldChatBox.length > 100){
        //删除前面一条
        commit('deleteWorldChatBox')
      }
    },
    //处理门派发言功能 聊天记录最多为100条
    handleMenpaiChat({commit,state},obj){
      //放入世界消息记录盒子
      commit('setMenpaiChatBox',obj)
      //如果聊天记录超过50条
      if(state.menpaiChatBox.length > 100){
        //删除前面一条
        commit('deleteMenpaiChatBox')
      }
    },

    //登录账号时 将账号存储到已登录的账号列表 大于列表大于3个时 不继续添加
    addAccountArrAction({commit,state}){
      //判断 已登录的账号是否超过 3个
      if(state.accountArr.length >3){
        return
      }
      else{
        //添加到已经登录账号
        commit('addAccountArr',{
          zhanghao:state.account.zhanghao,
          mima:state.account.mima,
          name:state.userInfo.name,
          active:true
        })
        //修改账号的active 将以前的改为false
        commit('changeActive',state.account.zhanghao)
      }
    }
  },
  getters:{
    //获取门派id
    getMenpaiId (state) {
      return state.userInfo.menpaiId
    }
  }
}
