import { createRouter, createWebHashHistory } from 'vue-router';
import store from '@/store';
const routes = [{
  path: '/',
  component: () => import('@/views/Layout.vue'),
  children: [// 重置路径
  {
    path: '/',
    redirect: '/home'
  }, // 首页
  {
    path: '/home',
    component: () => import('@/views/home')
  }, //功能
  {
    path: '/function',
    component: () => import('@/views/function')
  }, {
    path: '/chat',
    component: () => import('@/views/chat')
  }, {
    path: '/set',
    component: () => import('@/views/set')
  }]
}, {
  path: '/login',
  component: () => import('@/views/login')
}];
const router = createRouter({
  history: createWebHashHistory(),
  routes,

  //路由跳转的位置
  scrollBehavior(to, from, savedPosition) {
    return {
      left: 0,
      top: 0
    };
  }

}); //导航守卫 全局前置守卫

router.beforeEach(async (to, from, next) => {
  //取出token
  let token = store.state.user.token; //判断是否登录 token存在表示登录

  if (token) {
    //已登录还要去登录页
    if (to.path === '/login') {
      //跳转至首页
      next('/home');
    } else {
      //登录过了跳转至其他页 则获取最新的用户信息 相当于刷新
      //判断用户信息是否存在
      let hasUserInfo = !!store.state.user.userInfo.name;

      if (hasUserInfo) {
        //如果用户信息已经存在
        next();
      } else {
        try {
          //刷新
          await store.dispatch('user/userInfoUpdate'); //放行

          next();
        } catch (error) {
          ElMessage.error('获取最新信息失败，请重新登录'); //清空token 和用户信息

          store.commit('user/deleteAllInfo'); //跳转至登录页面

          next('/login');
        }
      }
    }
  } else {
    //未登录
    //除了当前是登录页的话不用跳转，其余都要跳转至登录页面
    if (to.path !== '/login') {
      next('/login');
    } else {
      //如果是登录页，直接放行
      next();
    }
  }
});
export default router;