import request from './request.js'

/**
 * 登录
 * @param {String} zhanghao - 用户账号
 * @param {String} mima - 用户密码
 * @param {String} banben -当前版本
 * @returns
 */
 export const loginApi = (userData) => {
  return request({
    url:'/login.d',
    method:'post',
    params:{
      ...userData,
      'banben':'1.21'
    }
  })
}


/**
 * 获取用户token
 * @param {String} zhanghao - 用户账号
 * @param {String} mima - 用户密码
 * @returns
 */
export const getUserTokenApi = (userData) => {
  return request({
    url:'/getToken.d',
    method:'post',
    params:userData
  })
}


/**
 * 数据刷新
 * @param {String} zhanghao - 用户账号
 * @param {String} mima - 用户密码
 * @param {String} banben -传入wap的话返回的数据里没有用户的反馈数据
 * @returns
 */
 export const userInfoUpdateApi = (userData) => {
  return request({
    url:'/getPersonData.d',
    method:'post',
    params:{
      ...userData,
      'banben':'wap'
    }
  })
}
