//工具函数

import store from '@/store';

// 用户信息数据转换为对象
export const changeStringToObject = (value) => {
  let str = value
  //替换逻辑
  let reg = new RegExp(', ','g')//g代表全部
  let reg2 = new RegExp('=','g')
  // str = str.replace(/,\s+/,'哈')//正确
  //数据处理
  //将 逗号空格替换成 引号逗号引号 等等
  str = str.replace(reg,'","')
  str = str.replace(reg2,'":"')
  str = str.replace('[','{"')
  str = str.replace(',]','"}')
  //将字符串转换为json对象
  str = JSON.parse( str )

  return str
}

//调用获取最新的token
export const getToken = async () => {
  //获取最新的token到state
  await store.dispatch('user/getUserToken',{
    zhanghao:store.state.user.account.zhanghao?store.state.user.account.zhanghao:'',
    mima: store.state.user.account.mima?store.state.user.account.mima:'',
  })
}
